:root {
  --text-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.navigation {
  align-items: end;
}

.navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.navigation__item {
  margin-right: 20px;
  line-height: 57px;
  list-style-type: none;
}

.navigation__item:last-child {
  margin: 0;
}

.navigation__link {
  text-transform: uppercase;
  color: #000;
  transition: .5 ease;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.navigation__link:hover {
  color: var(--text-color);
}

.navigation__link.is-active {
  color: var(--text-color);
  height: 60px;
  display: block;
}

.navigation__link.is-active:after {
  content: "";
  background-color: var(--text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
